@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
.aboutContainer{
    height: 70vh;
    margin-bottom: 2vw;
    padding: 4vw;
    border-bottom: 1px solid lavender;
    display: flex;
    justify-content: space-between;
}

.aboutContent{
    background-color: hsl(223deg 100% 99%);
    height: 50vh;
    padding: 2vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.aboutSmallHeading{
    font-size: smaller;
    font-weight: 800;
    color: #113cfc80;
}

.aboutTitle{
    font-size: larger;
    font-weight: 800;
    color: #113CFC;
    margin: 1rem 0;
}

.aboutBody{

}

.aboutImage{
    background-color: violet;
    width: 100%;
    height: 50vh;
    display: flex;
}

.aboutImage > img{
    width: 100%;
}

.aboutContent > button {
    margin-top: 2rem;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    border: none;
    color: #fff;
    display: flex;
    align-items: center;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: #1597E5;
}
.blogContainer{
    margin-bottom: 2vw;
    padding: 4vw;
    border-bottom: 1px solid lavender;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.blogSmallTitle{
    color: hsl(230deg 87% 66%);
}

.row{
    display: flex;
}

.blogTitle{
    font-size: x-large;
    font-weight: 800;
    color: #113CFC;
    margin: 1rem 0;
}
.blogNewsBody > span{
    margin-left: 2rem;
    display: flex;
    align-items: center;
    color: #1597E5;
    cursor: pointer;
    margin-bottom: 1.5rem;
    font-size: smaller;
}

.blogNewsBody{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 20vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 2rem;
    border-radius: 8px;
    box-shadow: 0px 0px 1px 0px;
}

.blogNewsImg{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
}

.blogNewsImg > img{
    width: 100%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.date{
    color: hsl(219deg 90% 24% / 47%);
    font-size: smaller;
    font-weight: 500;
    margin: 0.5rem 2rem;
}

.title{
    color: hsl(219deg 90% 24%);
    margin-left: 2rem;
    margin-bottom: 0.5rem;
    font-size: large;
    font-weight: 700;
}














.contactContainer{
    margin-bottom: 2vw;
    padding: 4vw;
    border-bottom: 1px solid lavender;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contactSmallTitle{
    color: hsl(230deg 87% 66%);
}

.contactTitle{
    font-size: x-large;
    font-weight: 800;
    color: #113CFC;
    margin: 1rem 0;
}

.row{
    display: flex;
}
.contactTypeContainer{
    width: 20vw;
    padding: 2rem;
    background-color: #1595e513;
    margin: 2rem;
}

.contactTypeIcon{
    font-size: xx-large;
    color: #1597E5;
}

.type{
    font-size: x-large;
    font-weight: 700;
    color: #113CFC;
}

.info{
    opacity: 0.75;
}

.content{
    height: 70vh;
    padding: 4vw;
    border-bottom: 1px solid lavender;
    background-image: url(/github.io/fintechstartup/static/media/1.2bd7bbaf.png);
    background-repeat: no-repeat;
    background-position: right;
}

.title > h1{
    font-size: 54px;
    font-weight: 500;
    color: #113CFC;
    line-height: 4rem;
}

.title > h3{
    color: grey;
    margin-top: 2vw;
    opacity: 75%;
    font-weight: 400;
}

.headerImg{
    display: flex;
    align-self: flex-end;
    justify-content: flex-end;
}

.title > button{
    margin-top: 2rem;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    border: none;
    color: #fff;
    display: flex;
    align-items: center;
    background-color: #1597E5;
}
.nav{
    padding: 2vw 4vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid lavender;
}

.list > ul{
    display: flex;
}

.list > ul > li{
    list-style: none;
    padding-right: 2vw;
    color: #1597E5;
    cursor: pointer;
    transition: 0.5s;
} 

.list > ul > li:hover{
    color: black;
}

.nav > button{
    padding: 0.5rem 1rem;
    border-radius: 5px;
    border: none;
    color: #fff;
    background-color: #1597E5;
    cursor: pointer;
}

.nav > button:hover{
    background-color: #1595e5be;
}
.servicesContainer{
    margin-bottom: 2vw;
    padding: 4vw;
    border-bottom: 1px solid lavender;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.servicesSmallTitle{
    color: hsl(230deg 87% 66%);
}

.row{
    display: flex;
}

.servicesTitle{
    font-size: x-large;
    font-weight: 800;
    color: #113CFC;
    margin: 1rem 0;
}
.serviceBody{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 25vw;
    padding: 4vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem;
    border-radius: 8px;
    box-shadow: 0px 0px 1px 0px;
}

.serviceIcon{
    font-size: xx-large;
}

.serviceTitle{
    font-size: x-large;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.7);
    margin: 0.5rem 0;
}

.serviceInfo{
    margin-top: 1rem;
    text-align: center;
    font-size: small;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.5);
}

.readMore{
    display: flex;
    align-items: center;
    margin-top: 2rem;
}
.teamMemberImg{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
}

.teamMemberBody{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 16vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 2rem;
    border-radius: 8px;
    box-shadow: 0px 0px 1px 0px;
}

.teamMemberImg > img{
    width: 100%;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}

.name{
    color: hsl(219deg 90% 24%);
    margin-top: 0.5rem;
    font-size: large;
    font-weight: 500;
}

.position{
    text-align: center;
    font-size: small;
    opacity: 0.5;
    color: rgba(0, 0, 0, 0.63);
}

.quote{
    font-style: italic;
    text-align: center;
    margin: 2rem;
    opacity: 0.75;
    font-size: small;
}
























.teamContainer{
    margin-bottom: 2vw;
    padding: 4vw;
    border-bottom: 1px solid lavender;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.teamSmallTitle{
    color: hsl(230deg 87% 66%);
}

.row{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.teamTitle{
    font-size: x-large;
    font-weight: 800;
    color: #113CFC;
    margin: 1rem 0;
}
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

@media screen and (max-width: 768px) {
    *{
        display: none;
    }
}
